import Upload from '../Upload'
/* import ChatFace from '../ChatFace' */
export default {
  data () {
    return {
      textImgVisible: false,
      base64: '',
      showFace: false
    }
  },
  methods: {
    sendMsg () {
      this.$emit('onSendMsg')
    },
    msgImgUploadPreview (base64) {
      console.log('111')
      this.textImgVisible = true
      this.base64 = base64
    },
    deleteMsgImg () {
      this.textImgVisible = false
      this.base64 = ''
    }
  },
  components: {
    Upload
    /* ChatFace */
  }
}



